@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100%;
}
.App {
  height: 100%;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  background-color: #a4b0be;

  font-family: 'Roboto', sans-serif;
  line-height: 1.2875;
}

.game-wrapper {
  margin-top: 50px;
  position: relative;
}

.game-wrapper .game-info {
  position: absolute !important;
  margin: 0;
  left: -20px;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  padding: 15px;
  border-radius: 5px;
  font-size: 20px;
  min-width: 170px;
  box-shadow: none !important;
  min-width: 290px;
}

.game-info .player {
  font-size: 25px;
  font-family: 'Permanent Marker', cursive;
}

.game-info .info > p {
  margin: 0 0 7px;
}

.board-row {
  display: flex;
}

.board-cell {
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 30px;
  font-weight: 900;
  font-family: 'Permanent Marker', cursive;

  margin: 1px;
  padding: 0;
  background-color: #fff;
  -webkit-transition: background-color 0.3s ease;
  transition: background-color 0.3s ease;
}

button.board-cell {
  outline: 0;
  border: 0;
  box-sizing: border-box;
  cursor: pointer;
}

.player.X {
  color: #eb4d4b;
}
.player.O {
  color: #4834d4;
}

.player.XO {
  color: #27ae60;
}

.board-cell.not-clickable,
.board-cell.not-clickable {
  cursor: not-allowed;
}

.board-cell.winning-cell {
  background-color: #ced6e0;
}

.result-wrapper {
  font-size: 30px;
}

.result.player {
  font-size: 90px;
  font-weight: 900;
  font-family: 'Permanent Marker', cursive;
}

.text-center {
  text-align: center !important;
}

/* history */

.history-block {
  position: absolute !important;
  top: 0;
  right: -20px;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  margin: 0 !important;
  min-width: 290px;
}

.history-block .history-title {
  font-size: 20px !important;
  margin: 0;
}

.history-block .box-title {
  display: flex;
  justify-content: space-between;
}
.history-block .history-items {
  margin-top: 10px;
  max-height: 400px;
  overflow: auto;
}

